<template>
    <div class="package">
        <h2></h2>
        <img class="lock" src="@/assets/.png" alt="">
        <div id="scroll" :v-model="dataPopRadio" class="solidUl" @scroll="scrollChange">
                <ul>
                    <li class="scrollli"
                        v-for="(item, index) in dataPopOption"
                        :key="index"
                    >{{item.label}}</li>
                </ul>
                <div class="backgroundFloat"></div> 
        </div>
        <count :num="initCount" @countChange="handleCountChange"></count>
        <button class="customButton" @click="handleButtonClick"></button>
    </div>
</template>

<script>
    import { jwtDecode } from 'jwt-decode';
    import Count from './smoking-count.vue';
    export default{
        components: {
            Count,
        },
        data(){
            return {
                dataPopOption : [{
                        name: '0',
                        label: ''
                    }, {
                        name: '1',
                        label: ''
                    }, {
                        name: '2',
                        label: ''
                    }, {
                        name: '3',
                        label: ''
                    }, {
                        name: '4',
                        label: ''
                    }, {
                        name: '5',
                        label: ''
                    }, {
                        name: '6',
                        label: ''
                    },
                ],
                brand: "",
                tag: 0,
                initCount: 0,
                changedCount: 0,
                expense: 0,
            };
        },
        methods: {
                scrollChange(e){
                    //li
                    var lis = document.getElementsByClassName('scrollli');
                        //li
                    for(var i = 0 ; i < lis.length; i++){
                        //offsetTopscrollTop
                        //lis[i].offsetTop-e.target.scrollTop
                        //li40px18522540
                        //185225
                        if((lis[i].offsetTop-e.target.scrollTop) > 44 && (lis[i].offsetTop-e.target.scrollTop) < 84){
                            //"active2"class
                            //"active"
                            lis[i].classList.remove("active2")
                            lis[i].classList.add("active")
                            this.tag = i;
                            //
                            for(var j = 1; j <= i; j++){
                                lis[i-j].style.fontSize = (20-3.5*j) + 'px';
                            }
                            //
                            for(var a = 1; a < lis.length-i; a++){
                                lis[i+a].style.fontSize = (20-3.5*a)+'px';
                            }
                            //"active""active2"
                            }else{
                                lis[i].classList.remove("active");
                                lis[i].classList.add("active2");
                            }
                    }
                },
                handleCountChange(newCount){
                    this.changedCount = newCount;
                },
                handleButtonClick(){     //
                    //  token  localStorage  JWT
                    const token = localStorage.getItem("token");
                    //  JWT
                    //console.log(token)
                    const decodedToken = jwtDecode(token);
                    // 
                    console.log(decodedToken)
                    const userAccount = decodedToken.sub;
                    // userAccount JWT
                    console.log("", userAccount);
                    const today = new Date();
                    const formattedDate = today.toLocaleDateString(); // Get local date in 'YYYY-MM-DD' format
                    console.log(this.changedCount)
                    console.log(this.tag)
                    
                 switch (this.tag) {
                            case 0:
                                this.expense = this.changedCount * 0.7;
                                break;
                            case 1:
                                this.expense = this.changedCount * 0.5;
                                break;
                            case 2:
                                this.expense = this.changedCount * 0.95;
                                break;
                            case 3:
                                this.expense = this.changedCount * 1.15;
                                break;
                            case 4:
                                this.expense = this.changedCount * 3.25;
                                break;
                            case 5:
                                this.expense = this.changedCount * 0.8;
                                break;
                            case 6:
                                this.expense = this.changedCount * 1.15;
                                break;
                            default:
                                // 
                                this.expense = 0; // 
                                //  throw new Error('Unexpected tag value: ' + this.tag);
                    }
                    console.log('' + this.expense)
                    console.log(formattedDate)

                     fetch('http://localhost:3000/api/user/record', {
                        method: 'POST',
                        headers: {
                        'Content-Type': 'application/json',
                        },
                        body: JSON.stringify({
                            account: userAccount,
                            date:  formattedDate,
                            smokingType: this.tag,
                            smokingAmount: this.changedCount,
                            smokingExpenses: this.expense,
                        }),
                    })
                    .then(response => response.json())
                    .then(data => {
                    if (data.success) {
                        alert('')
                    } else {
                        console.error("");
                    }
                    })
                    .catch(error => {
                        console.error(":", error);
                    });
                    },
                },  
                mounted() {


                },

            };
</script>

<style scoped>
    .package{
        position: absolute;
        width: 400px;
        height: 400px;
        left: 35%;
        top: 20%;
        box-shadow: 10px 10px 10px 10px rgba(0, 0, 0, 0.2);
        /* border: 1px solid black; */
        background-color: rgb(248, 241, 228);
        transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
        border-radius: 20px; 
        cursor: pointer;
    }
    .package:hover {
        transform: scale(1.1); /* 1.1 */
        box-shadow: 10px 10px 10px 10px rgba(0, 0, 0, 0.5); /*  */
    }
    .package h2{
        position: absolute;
        top: 25px;
        left: 36% ;
        color: #333;
    }
    .package .lock{
        position: absolute;
        top: 25px;
        right: 10% ;
        width: 30px;
        height: 30px;
    }
    .solidUl {
        /* border: 1px solid black; */
        width: 300px;
        padding-top: 40px;
        margin-top: 15px;
        height: 150px;
        overflow-y: scroll;
        touch-action: pan-y;
        -webkit-overflow-scrolling: touch;
        position: absolute;
        left: 11%;
        top: 20%;
    }
    .solidUl::-webkit-scrollbar {
        display: none;
    }
    ul {
        padding: 80px 0 230px 0;
        margin: 0;
        background-color: rgb(248, 241, 228);
        position: absolute;
        top: 0;
        width: 100%;
        height: 400px;
        /* border: solid 1px red; */
    }
    li {
        list-style: none;
        font-size: 18px;
        line-height: 20px;
        text-align: center;
        opacity: 0.3;
        height: 40px;
        background-color: rgb(248, 241, 228);
    }
    .active {
        font-weight: 400;
        font-size: 20px !important;
        color: #333;
        opacity: 1.2 !important;
        top: 150px;
    }
    .active2 {
        color: #333;
        opacity: 0.6 !important;
    }
    .backgroundFloat {
        width: 300px;
        height: 40px;
        background-color: #d1d1d1;
        opacity: 0.2;
        position: sticky;
        top: 20%;
    }
    .customButton {
        background-color: #4caf50;
        color: white;
        padding: 10px 20px;
        font-size: 16px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        margin-top: 20px;
        position: absolute;
        top: 78%;
        left: 39%  ;
    }
    .customButton:hover {
         /*  */
        background-color: #45a049;
    }
</style>