<template>
  <div id="app">
      <div class="SwiperBox" @mouseenter="MouseFun('')" @mouseleave="MouseFun('')">
          <!--  -->
          <img :class="['imgCss',ShowImg==index?'ShowCss':'']"
          :src="item.imgUrl" v-for="(item,index) in imgList" :key="index" />
          <!--  -->
          <div class="leftBtn" @click="throttle(PrevFun)"><img class="leftattr" src="@/assets/left.png" alt=""></div>
          <!--  -->
          <div class="rightBtn" @click="throttle(NextFun)"><img class="rightattr" src="@/assets/right.png" alt=""></div>
          <!--  -->
          <div class="instBox">
            <div v-for="(item,index) in imgList.length" :key="index"
            @click="ShowImg=index" :class="['inst',ShowImg==index?'instActv':'']">
            </div>
          </div>
      </div>
  </div>
</template>
<script>
  export default {
    name:'img-carousel',
    components: {},
    data() {
      return {
       imgList: [
          // {imgUrl: '@/assets/pro1.jpg'},
          // {imgUrl: '@/assets/pro2.jpg'},
          // {imgUrl: '@/assets/pro3.jpg'},     //,404
          // {imgUrl: '@/assets/pro4.jpg'},
          // {imgUrl: '@/assets/pro5.jpg'}
          {imgUrl:require('@/assets/background1.jpg')},
          {imgUrl:require('@/assets/pro2.jpg')},
          {imgUrl:require('@/assets/pro3.jpg')},
          {imgUrl:require('@/assets/pro4.jpg')},
          {imgUrl:require('@/assets/background2.png')},
        ],
        ShowImg:0,  // 
        flag:true, // 
        start:null, // 
      };
    },
    mounted() {
      this.setTimeoFun()
    },
    methods: {
      MouseFun(type){//             // 
        type==''?clearTimeout(this.start):this.setTimeoFun()
      },
      setTimeoFun(){
          this.start = setInterval(()=>{
          this.NextFun()
        },1500)
      },
      //  PrevFun()  NextFun(),
      throttle(fun) {
      if (this.flag) {
            this.flag = false;
            fun(); // PrevFun()NextFun()
            setTimeout(() => {
            this.flag = true;
            }, 1200); // 
        }
      },
      // 
      PrevFun(){
        if(this.ShowImg!==0){
           this.ShowImg--
        }else{
           this.ShowImg=this.imgList.length-1
        }
      },
      // 
      NextFun(){
        if(this.ShowImg!==this.imgList.length-1){
           this.ShowImg++
        }else{
           this.ShowImg=0
        }
      },
    }
  };
</script>
<style scoped>
   /*  */
  .SwiperBox {
    position: relative;
    width: 100%;
    height: 500px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    cursor: pointer;
  }
  /*  */
  .imgCss {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 500px;
    opacity: 0;
    transition: 0.8s;  /*  */
  }
  /* () */
  .ShowCss {
    opacity: 1;
  }
   /* , */
  .leftBtn,
  .rightBtn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: rgba(109, 109, 109, 0.445); */
    /* color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-size: 12px;
    font-weight: 500; */
  }
  .leftBtn {
    left: 10px;
  }
  .rightBtn {
    right: 10px;
  }
  /*  */
  .instBox{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10px;
    display: flex;
  }
  .leftattr,
  .rightattr{
    width: 30px;
  }
   /*  */
  .inst{
    width: 10px;
    height: 10px;
    border: 1px solid #ccc;
    margin-right: 8px;
    background: #fff;
    border-radius: 50%;
    cursor: pointer;
  }
  .inst:last-child{
    margin-right: 0px;
  }
  .instActv{
    border: 1px solid #ff0000;
    background: #ff0000;
  }
  #app{
    width: 100%;
    /* padding: 120px; */
    display: flex;
    justify-content: center;
  }

</style>
