<template>
  <div>
    <Side></Side>
    <div class="canv">
      <h2></h2>
      <Drag :MenuTitle="MenuTitle" :MenuItems="MenuItems"  @menu-item-click="selectYear"></Drag>
      <highcharts :options="chartOptions"></highcharts>
    </div>
  </div>
</template>

<script>
import Side from './side-bar.vue';
import Drag from './Drop-downMenu.vue';
import { jwtDecode } from 'jwt-decode';
export default {
  components: {
    Side,
    Drag
  },
  data() {
    return {
      chartOptions: {},
      MenuTitle: '',
      MenuItems: ['', '', '', '', ''],
      dataSet: {
        data1: [300, 324, 241, 456, 123, 430, 978, 974, 675, 457, 342, 223],
        data2: [320, 324, 341, 356, 379, 380, 438, 474, 575, 657, 742, 823],
        data3: [320, 524, 541, 656, 979, 1380, 238, 774, 975, 157, 242, 223],
        data4: [120, 224, 341, 456, 579, 6380, 738, 874, 975, 1057, 1142, 1223],
        data5: [122, 324, 341, 456, 579, 580, 638, 874, 975, 1057, 1142, 1223],
      },
      select: [],   // 
    };
  },
  mounted() {
    this.selectYear('');  // 
  },
  methods: {
    // 
    selectYear(item) {
          // Account
      const token = localStorage.getItem("token");
      const decodedToken = jwtDecode(token);
      const userAccount = decodedToken.sub;
      var yearId = 5;
      // 
      console.log(item)
      if (item === '') {
        yearId = 1;
      } else if (item === '') {
        yearId = 2;
      } else if (item === '') {
        yearId = 3;
      } else if (item === '') {
        yearId = 4;
      } else if (item === '') {
        yearId = 5;
      }
      fetch('http://localhost:3000/api/user/monthly', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
             account: userAccount,
             yearId: yearId             
            }),
          })
          .then(response => response.json())
          .then(data => {
 
            if (data.success) {
              console.log('monthlySum:');
              console.log(data.monthlySum);
              this.select = data.monthlySum;
              this.getChart(item);
              return this.select;     // 
            } else {
              console.log(data.error);
              console.error("");
            }
          })
          .catch(error => {
            console.error(":", error);
          });
    },
    getChart(item) {
      var template = {
        title: {
          text: item + "",
        },
         accessibility: {
          enabled: false, //  false
        },
        xAxis: {
          categories: ["", "", "", "", "", "", "",
            "", "", "", "", ""],
          labels: {
            style: {
              color: '#333', // x
              fontSize: '14px', // x
            },
          },
        },
        yAxis: {
          title: { text: "/" },
          labels: {
            style: {
              color: '#333', // y
              fontSize: '14px', // y
            },
          },
        },
        credits: {
          enabled: false,
        },
        plotOptions: {
          line: {
            color: "#9b59b6", // 
            lineWidth: 2, // 
            marker: {
              enabled: true,
              symbol: 'circle', // 
              radius: 6, // 
            },
            dataLabels: {
              enabled: true,
              color: '#333', // 
              fontSize: '12px', // 
              y: -15, // 
            },
            shadow: {
              color: 'rgba(0, 0, 0, 0.5)', // 
              offsetX: 5, // 
              offsetY: 5, // 
              opacity: 0.75, // 
              width: 2, // 
            },
          },
        },
        series: [
          {
            name: "",
            data: this.select,  // 
          },
        ],
      };
      this.chartOptions = template;
    },
  },
};

</script>

<style scoped>
  .canv {
    position: absolute;
    margin-left: 220px;
    margin-top: 80px;
    background-color: white;
    width: 83%;
    height: 85%;
    border-radius: 15px;
  }

  .canv h2 {
    margin: 10px;
    padding: 5px;
    border-left: 10px solid #9b59b6;
    width: 160px;
  }
</style>
