<template>
  <div>
    <Side></Side>
    
    <div class="canv">
      <h2></h2>
      <Drag :MenuTitle="MenuTitle" :MenuItems="MenuItems" @menu-item-click="selectWeek"></Drag>
      <highcharts :options="chartOptions"></highcharts>
    </div>
  </div>
</template>

<script>
import Side from './side-bar.vue';
import Drag from './Drop-downMenu';
import { jwtDecode } from 'jwt-decode';
export default {
  components: {
    Side,
    Drag
  },
  data() {
    return {
      chartOptions: {},
      MenuTitle: '',
      MenuItems: ['', '', '', ''],
      dataSet: {
        data1: [120, 124, 141, 156, 123, 230, 278],
        data2: [120, 224, 141, 156, 179, 180, 138],
        data3: [220, 124, 141, 156, 179, 180, 138],
        data4: [120, 124, 141, 156, 179, 180, 138]
      },
      select: [],   // 4
    };
  },
  mounted() {
    this.selectWeek("");
  },
  methods: {
    // 
     
    selectWeek(item) {

      // Account
      const token = localStorage.getItem("token");
      const decodedToken = jwtDecode(token);
      const userAccount = decodedToken.sub;
      var weekId = 4;

        if (item === '') {
          //this.select = this.dataSet.data1;
          weekId = 1;
        } else if (item === '') {
          //this.select = this.dataSet.data2;
          weekId = 2;
        } else if (item === '') {
          //this.select = this.dataSet.data3;
          weekId = 3;
        } else if (item === '') {
          //this.select = this.dataSet.data4;
          weekId = 4;
        }
        fetch('http://localhost:3000/api/user/weekly', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
             account: userAccount,
             weekId: weekId             
            }),
          })
          .then(response => response.json())
          .then(data => {
 
            if (data.success) {
              console.log(data.weeklySum)
              this.select = data.weeklySum;
              this.getChart(item);
              return this.select;     // 
            } else {
              console.log(data.error);
               console.error("");
            }
          })
          .catch(error => {
            console.error(":", error);
          });

        
    },
    getChart(item) {
      var template = {
        title: {
          text: item  + "",
        },
        xAxis: {
          categories:["", "", "", "", "", "", ""],
          labels: {
            style: {
              color: '#333', // x
              fontSize: '14px', // x
            },
          },
        },
        yAxis: {
          title: { text: "/" },
          labels: {
            style: {
              color: '#333', // y
              fontSize: '14px', // y
            },
          },
        },
        credits: {
          enabled: false,
        },
        plotOptions: {
          line: {
            color: "#0e6145", // 
            lineWidth: 2, // 
            marker: {
              enabled: true,
              symbol: 'circle', // 
              radius: 6, // 
            },
            dataLabels: {
              enabled: true,
              color: '#333', // 
              fontSize: '12px', // 
              y: -15, // 
            },
            shadow: {
              color: 'rgba(0, 0, 0, 0.3)', // 
              offsetX: 4, // 
              offsetY: 4, // 
              opacity: 0.7, // 
              width: 4, // 
            },
          },
        },
        series: [
          {
            name: "",
            data: this.select,
          },
        ],
      };
      this.chartOptions = template;
    },
  },
};
</script>

<style scoped>
.canv {
  position: absolute;
  margin-left: 220px;
  margin-top: 80px;
  background-color: white;
  width: 83%;
  height: 85%;
  border-radius: 15px;
}

.canv h2 {
  margin: 10px;
  padding: 5px;
  border-left: 10px solid seagreen;
  width: 160px;
}
</style>
